import './app.scss';
import MainPage from './components/mainpage/mainPage'
import Groups from './components/victorigroup/victoriGroup'
import NewGroup from './components/newGroup/newgroup'
import Vacancy from './components/vacancy/vacancy'
import Contacts from './components/contacts/contacts'

import Contact from './components/icons/phone_purple.svg'
import Group from './components/icons/group_purple.svg'
import Info from './components/icons/info_purple.svg'
import Job from './components/icons/vacancy_purple.svg'

import GroupVt from './components/victorigroup/pagesMobile/vt'
import SearchV from './components/vacancy/search/search'





import {
  BrowserRouter as Router, Routes, Route, Link
} from "react-router-dom";


function App() {
  return (
    <Router>
        <div className="column">
          <div className="menu">
              <Link to="/victori_group/">
                  <img src={Info} alt=""/>
              </Link>
              <Link to="/victori_group/new">
                  <img src={Group} alt=""/>
              </Link>
              <Link to="/victori_group/vacancy">
                  <img src={Job} alt=""/>
              </Link>
              <Link to="/victori_group/contacts">
                  <img src={Contact} alt=""/>
              </Link>
          </div>
        </div>
        
        <Routes>
          <Route path="/victori_group/vacancy/search" element={SearchV}></Route>
        </Routes>
        <Routes>
          <Route path="/victori_group/group/vt" element={GroupVt}></Route>
        </Routes>
        <Routes>
          <Route path="/victori_group/new" element={NewGroup}></Route>
        </Routes>
        <Routes>
          <Route path="/victori_group/contacts" element={Contacts}></Route>
        </Routes>
        <Routes>
          <Route path="/victori_group/vacancy" element={Vacancy}></Route>
        </Routes>
        <Routes>
          <Route path="/victori_group/group" element={Groups}></Route>
        </Routes>
        <Routes>
          <Route path="/victori_group/" element={MainPage}></Route>
        </Routes>
    </Router>
  );
}

export default App;
